body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.HeaderComponent{
  background: #fddd2d;
  width: 100%;
  display: flex;
  padding: 20px;
  border-bottom: 1px solid #d2d2d4;
}

.contentUsers {
  float: left;
  width: 100%;
  height: 100%;
  padding: 0 5%;
  font-size: 1em;
}

.divButtonNuevo{
  text-align: right;
  width: 50%;
}
.divInputSearch{
  text-align: left;
  width: 50%;
}
.divHeader{
  width: 100%;
  display: flex;
}
.buttonNuevo {
  font-size: 18px;
  letter-spacing: 0.7px;
  background: #0470dc !important;
  color: white !important;
  margin-bottom: 16px;
  border: 1px solid black !important;
}

.contentUsers .buttonNuevo:hover{
  background: #0470dc87 !important;
  color: #ffffff !important;
  border: 1px solid #0470dc87!important;
}

.bodyTableUsers{
  height: 400px;
  overflow: scroll;
  border: 1px solid black;
}
.footerUsers{
  background: #0470dc;
  height: 100px;
  color: white;
  text-align: center
}
.divFooterMessage{
  width: 100%;
}
.divFooterIcons{
  width: 100%;
  font-size: 25px;
}
.headerModal{
  background: #0470dc !important;
  color: white !important;
}

.buttonModal {
  width: 100%;
  margin-top: 15px !important;
  font-size: 18px;
  letter-spacing: 0.7px;
  background: #0470dc !important;
  color: white !important;
  margin-bottom: 16px;
  border: 1px solid black !important;
}

.messageVacio{
  color: gray;
  font-size: 30px;
}
.contentMessageUsers{
  text-align: center;
  margin-top: 15%;
}
.divBodyTable{
  width: 105%;
  margin-top: 25px;
  overflow-y: scroll;
  height: 450px;
}
.tableUsers{
  width: 96% !important;
}
.divLineBottomTitle{
  border-bottom: 3px solid #0270dc;
  margin-top: -10px;
  width: 5%;
  margin-bottom: 20px;
}